import React from 'react';
import './Navbar.css'
import logo from '../fot/logo.png'
import Button from '../comp/button/button'
import {
    Link
  } from "react-router-dom";
export default function Navbar(){
    return(
            <div className='nav'>
                <div className='logo'> 
                <img alt='logo' width='125' height='50' src={logo}></img>
                </div>
                <div className='navlinks'>
                <Link to='/projectone'>
                <Button
                >   
                    Główna
                </Button>
                </Link>
                <Link to='/produkty'>
                <Button>   
                    Produkty
                </Button>
                </Link>
                <Link to='/projekty'>
                <Button
                >   
                    projekty
                </Button>
                </Link>
                <Link to='/contact'>
                <Button buttonStyle='active'
                >   
                    kontakt
                </Button>
                </Link>
                    </div>
            </div>
    )
}